import { useState, useEffect } from "react";

import AppContext from "./AppContext";

const AppStateContext = (props) => {
  const [activeTab, setActiveTab] = useState("home");
  const [activeStepItem, setActiveStepItem] = useState(null);
  const [progress, setProgress] = useState(0);
  const [user, setUser] = useState(null);

  const [patientMetaData, setPatientMetaData] = useState([]);

  const [presentationButtonActive, setPresentationButtonActive] =
    useState(false);

  const [isPlaying, setIsPlaying] = useState(false);
  const [activeStepRunning, setActiveStepRunning] = useState(false);

  const [totalTime, setTotalTime] = useState({ min: 0, sec: 0 });
  const [activeStepTime, setActiveStepTime] = useState({ min: 0, sec: 0 });

  // An array of steps with associated data
  const [stepsArray2, setStepsArray2] = useState([
    {
      stepNumber: 1,
      stepText: "Step 1",
      isStepCompleted: false,
      isInProcess: false,
      isActive: false,
      timeSpent: { min: 0, sec: 0 },
      stepQuestionsList: [
        {
          question:
            "Which of the following keywords is used to define a variable in Javascript?",
          options: ["var", "let", "Both A and B", "None of the above"],
          answer: "Both A and B",
          userAnswer: "",
        },
        {
          question:
            "Which of the following methods is used to access HTML elements using Javascript?",
          options: [
            "getElementById()",
            "getElementByClassName()",
            "Both A and B",
            "None of the above",
          ],
          answer: "Both A and B",
          userAnswer: "",
        },
        {
          question: "How can a datatype be declared to be a constant type?",
          options: ["const", "var", "let", "constant"],
          answer: "const",
          userAnswer: "",
        },
      ],
    },
    {
      stepNumber: 2,
      stepText: "Step 2",
      isStepCompleted: false,
      isInProcess: false,
      isActive: false,
      timeSpent: { min: 0, sec: 0 },
    },
    {
      stepNumber: 3,
      stepText: "Step 3",
      isStepCompleted: false,
      isInProcess: false,
      isActive: false,
      timeSpent: { min: 0, sec: 0 },
    },
    {
      stepNumber: 4,
      stepText: "Step 4",
      isStepCompleted: false,
      isInProcess: false,
      isActive: false,
      timeSpent: { min: 0, sec: 0 },
    },
    {
      stepNumber: 5,
      stepText: "Step 5",
      isStepCompleted: false,
      isInProcess: false,
      isActive: false,
      timeSpent: { min: 0, sec: 0 },
    },
    {
      stepNumber: 6,
      stepText: "Step 6",
      isStepCompleted: false,
      isInProcess: false,
      isActive: false,
      timeSpent: { min: 0, sec: 0 },
      stepQuestionsList: [
        {
          question:
            "Which of the following keywords is used to define a variable in Javascript?",
          options: ["var", "let", "Both A and B", "None of the above"],
          answer: "Both A and B",
          userAnswer: "",
        },
        {
          question:
            "Which of the following methods is used to access HTML elements using Javascript?",
          options: [
            "getElementById()",
            "getElementByClassName()",
            "Both A and B",
            "None of the above",
          ],
          answer: "Both A and B",
          userAnswer: "",
        },
        {
          question: "How can a datatype be declared to be a constant type?",
          options: ["const", "var", "let", "constant"],
          answer: "const",
          userAnswer: "",
        },
      ],
    },
  ]);

  // useEffect to track and update the total time
  useEffect(() => {
    let interval = null;

    if (isPlaying) {
      interval = setInterval(() => {
        setTotalTime((prevTotalTime) => {
          let newSec = prevTotalTime.sec + 1;
          let newMin = prevTotalTime.min;
          if (newSec === 60) {
            newSec = 0;
            newMin += 1;
          }
          return { min: newMin, sec: newSec };
        });
      }, 1000);
    } else {
      clearInterval(interval);
    }

    return () => clearInterval(interval);
  }, [isPlaying]);

  // useEffect to track and update the time for the active step
  useEffect(() => {
    let activeInterval = null;

    if (activeStepRunning) {
      activeInterval = setInterval(() => {
        setActiveStepTime((prevStepTime) => {
          let newSec = prevStepTime.sec + 1;
          let newMin = prevStepTime.min;
          if (newSec === 60) {
            newSec = 0;
            newMin += 1;
          }
          return { min: newMin, sec: newSec };
        });
      }, 1000);
    } else {
      clearInterval(activeInterval);
    }

    return () => clearInterval(activeInterval);
  }, [activeStepRunning]);

  // Provides state and functions to the components within the context
  return (
    <AppContext.Provider
      value={{
        activeTab,
        setActiveTab,
        activeStepItem,
        setActiveStepItem,
        stepsArray2,
        setStepsArray2,
        totalTime,
        setTotalTime,
        activeStepTime,
        setActiveStepTime,
        isPlaying,
        setIsPlaying,
        activeStepRunning,
        setActiveStepRunning,
        presentationButtonActive,
        setPresentationButtonActive,
        progress,
        setProgress,
        user,
        setUser,
      }}
    >
      {props.children}
    </AppContext.Provider>
  );
};

export default AppStateContext;
